#{$global}-job {
    &__header {
        display: flex;
        justify-content: space-between;
    }
    &__company {
        @extend .poppins-bold;
        font-size: 18px;
        color: white;
        display: inline-block;
        &::after {
            content: '/';
            padding: 0 5px;

            @media screen and (max-width: $tab-maxw){
                content: none;
                padding: 0;
            }
        }
    }
    &__title {
        @extend .poppins-regular;
        font-size: 18px;
        color: white;
        display: inline-block;
        //padding-left: 5px;

        @media screen and (max-width: $mobile-maxw) {
            font-size: 12px;
            //padding-left: 0;
        }
    }
    &__period {
        @extend .poppins-regular;
        font-size: 12px;
        color: map-get($gray, "e1e1e1");
        text-transform: uppercase;
    }
    &__desc {
        @extend .poppins-medium;
        font-size: 12px;
        color: map-get($gray, "e1e1e1");
    }
}