/* layout files */
.portfolio-header {
  height: 5.7142857143rem;
  max-height: 5.7142857143rem;
  width: 100%;
  position: sticky;
  top: 0;
  left: 0;
}
.portfolio-header__content {
  display: flex;
  justify-content: space-between;
  width: calc(100% - 40px);
  margin: auto;
  padding: 1.4285714286rem 1.4285714286rem 0 1.4285714286rem;
}
@media screen and (max-width: 1081px) {
  .portfolio-header__content {
    width: calc(100% - 40px);
    padding: 1.4285714286rem;
  }
}
@media screen and (min-width: 1080px) {
  .portfolio-header__content {
    width: 73.1428571429rem;
  }
}
.portfolio-header__fname {
  font-size: 22px;
  color: white;
}
@media screen and (max-width: 1081px) {
  .portfolio-header__fname {
    font-size: 18px;
  }
}
.portfolio-header__lname {
  font-size: 10px;
  color: white;
}
.portfolio-header__btn-link {
  color: #e1e1e1;
  border-radius: 8px;
  padding: 10px;
  height: 15px;
  border: 1px solid #9a9a9a;
  display: inline-block;
  text-decoration: none;
}
.portfolio-header__btn-link:hover {
  color: black;
  background-color: white;
}

.portfolio-main {
  width: 100%;
  height: calc(100% - 160px);
  overflow-y: auto;
  padding: 2.8571428571rem 0;
}
.portfolio-main-content {
  background-image: url("/src/assets/images/background-pattern.png");
  background-repeat: no-repeat;
  width: calc(100% - 20px);
  margin: auto;
}
@media screen and (max-width: 1081px) {
  .portfolio-main-content {
    width: calc(100% - 40px);
    padding: 0 1.4285714286rem;
  }
}
@media screen and (min-width: 1080px) {
  .portfolio-main-content {
    width: 73.1428571429rem;
  }
}
.portfolio-main__section {
  padding: 1.4285714286rem 0;
}
.portfolio-main__footer {
  min-height: 3.5714285714rem;
  padding: 1.4285714286rem;
  width: 71.7142857143rem;
  margin: auto;
  text-align: center;
}
@media screen and (max-width: 1081px) {
  .portfolio-main__footer {
    width: calc(100% - 40px);
  }
}

.portfolio-navoverlay {
  width: 100%;
  height: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  opacity: 0;
  visibility: hidden;
  background: rgba(0, 0, 0, 0.9);
  transition: opacity 0.35s, visibility 0.35s, height 0.6s ease;
  -webkit-transition: opacity 0.35s, visibility 0.35s, height 0.6s ease;
}
.portfolio-navoverlay.active {
  width: 100%;
  height: 100%;
  opacity: 100;
  visibility: visible;
}
.portfolio-navoverlay__header {
  height: 0.7142857143rem;
  padding: 0.7142857143rem;
}
.portfolio-navoverlay__content {
  height: calc(100% - 30px);
}
.portfolio-navoverlay__content ul {
  list-style-type: none;
  margin: 0;
  padding: 0.7142857143rem 0;
}
.portfolio-navoverlay__content ul > li {
  height: 1.4285714286rem;
  padding: 0.7142857143rem;
  font-size: 18px;
}
.portfolio-navoverlay__content ul > li a {
  text-decoration: none;
  color: white;
}
.portfolio-navoverlay__content ul > li:hover, .portfolio-navoverlay__content ul > li:focus, .portfolio-navoverlay__content ul > li:active {
  background: white;
}
.portfolio-navoverlay__content ul > li:hover a, .portfolio-navoverlay__content ul > li:focus a, .portfolio-navoverlay__content ul > li:active a {
  text-decoration: underline;
  color: black;
}

/* component files */
.portfolio-dp {
  height: 16.5rem;
  width: 16.5rem;
  background-color: #1a1a1a;
  border-radius: 100%;
  margin: auto;
  background-image: url("/src/assets/images/self.jpg");
  background-size: cover;
}

.portfolio-tag {
  height: 20px;
  border-radius: 20px;
  background: linear-gradient(to right, purple, orange);
  margin: 2px 5px;
  box-sizing: content-box;
}
.portfolio-tag__body {
  height: 15px;
  padding: 0px 7px 1px 7px;
  margin: 0px 1px;
  color: white;
  font-size: 10px;
  background: black;
  border-radius: 20px;
  cursor: pointer;
  display: inline-block;
}

.portfolio-job__header {
  display: flex;
  justify-content: space-between;
}
.portfolio-job__company {
  font-size: 18px;
  color: white;
  display: inline-block;
}
.portfolio-job__company::after {
  content: "/";
  padding: 0 5px;
}
@media screen and (max-width: 1081px) {
  .portfolio-job__company::after {
    content: none;
    padding: 0;
  }
}
.portfolio-job__title {
  font-size: 18px;
  color: white;
  display: inline-block;
}
@media screen and (max-width: 481px) {
  .portfolio-job__title {
    font-size: 12px;
  }
}
.portfolio-job__period {
  font-size: 12px;
  color: #e1e1e1;
  text-transform: uppercase;
}
.portfolio-job__desc {
  font-size: 12px;
  color: #e1e1e1;
}

.portfolio-project {
  position: relative;
}
.portfolio-project__content {
  position: relative;
  width: 60%;
  z-index: 2;
}
@media screen and (max-width: 1081px) {
  .portfolio-project__content {
    z-index: 0;
    width: 100%;
  }
}
.portfolio-project__title {
  font-size: 18px;
  color: white;
  margin: 10px 0;
}
.portfolio-project__desc {
  min-height: 5.7142857143rem;
  position: relative;
  background-color: rgba(88, 89, 93, 0.6);
  border-radius: 20px;
}
.portfolio-project__desc::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  filter: blur(10px);
  -webkit-filter: blur(10px);
  width: 100%;
  background: linear-gradient(to right, rgba(128, 0, 128, 0.35), rgba(255, 165, 0, 0.35));
  background-position: center;
  background-repeat: no-repeat;
  height: 100%;
}
.portfolio-project__desc-text {
  width: calc(100% - 20px);
  color: white;
  font-size: 12px;
  padding: 10px;
}
@media screen and (max-width: 1081px) {
  .portfolio-project__desc {
    border-radius: 0;
  }
}
.portfolio-project__snapshot {
  position: absolute;
  right: 0;
  top: 0;
  content: "";
  background: #222;
  height: 10.7142857143rem;
  width: 32.8571428571rem;
  cursor: pointer;
}
.portfolio-project__snapshot-image {
  height: 100%;
  width: 100%;
  object-fit: scale-down;
}
.portfolio-project__snapshot:hover {
  border: 2px solid #e1e1e1;
}
@media screen and (max-width: 1081px) {
  .portfolio-project__snapshot {
    width: 100%;
    height: auto;
    position: relative;
  }
}

.portfolio-btn-transparent {
  background-color: transparent;
  border: none;
}

